@keyframes neon-pulse {
  0%, 100% {
    background-position: 0% 50%;
    filter: brightness(1) saturate(1.2);
    box-shadow: 0 0 20px rgba(34, 211, 238, 0.5), 0 0 40px rgba(168, 85, 247, 0.3);
  }
  50% {
    background-position: 100% 50%;
    filter: brightness(1.2) saturate(1.5);
    box-shadow: 0 0 30px rgba(34, 211, 238, 0.8), 0 0 60px rgba(168, 85, 247, 0.5);
  }
}
